var vid = document.getElementById("background") as HTMLVideoElement;

window.addEventListener("scroll", () => 
{
    let f =  screenpos(window.scrollY, document.body.scrollHeight, window.innerHeight);
    vid.currentTime = f;
}
)


function screenpos(x,y,z) {
    let a = (x / (y - z));
    a = a /1.1;
    return a * 2;
}